import React from 'react'
import {useState} from 'react'

import {useDispatch,useSelector} from 'react-redux'
import {addCurrent} from '../Actions/Index'
import { Link } from 'react-router-dom';

import '../index.css'
const Login = () => {
    const list = useSelector((state)=>state.adminReducers.list);
    const dispatch = useDispatch();
    const [LoginMob, setLoginMob] = useState('');
    const [LoginPass, setLoginPass] = useState("");
    const click=()=>{

     const temp=list.find((curr)=>curr.mob===LoginMob);
     if(temp!==undefined && temp.password===LoginPass){
        dispatch(addCurrent(temp.id,temp.fname,temp.address,temp.mob,temp.password,temp.admin));
     }
     else
        alert("Incorrect Mobile no. or Password")
    }
   

    return (
        <div>
            
         <form >
            <div className="mb-3">
                <label htmlFor="mob1" className="form-label">Mobile no.</label>
                <input type="text" className="form-control" id="mob1" aria-describedby="mobHelp" value={LoginMob} onChange={(e)=>setLoginMob(e.target.value)}></input>
                <div id="mobHelp" className="form-text">We'll never share your contacts with anyone else.</div>
            </div>
            <div className="mb-3">
                <label htmlFor="pass1" className="form-label">Password</label>
                <input type="password" className="form-control" id="pass1" value={LoginPass} onChange={(e)=>setLoginPass(e.target.value)}></input>
            </div>
            
            <button onClick={click} className="btn btn-primary"><Link id='link' to="/Account">Submit</Link></button>
         </form>
         
        </div>
    )
}

export default Login
