import React from 'react'
import {
    Link
} from "react-router-dom";
import '../index.css'
  
import Login from './Login'



const AdminLogin = () => {
   
    return (
        <>
        <div className='memberLogin container-fluid'>
            <div className='loginForm'>
           <ul className="nav nav-pills nav-fill">
                <li className="nav-item">
                    <Link className="nav-link" id="navMember" aria-current="page" to="/MemberLogin">Member</Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link active" id="navAdmin" to="/AdminLogin">Admin</Link>
                </li>
                </ul>
                <Login/>
                </div>
        </div>
        </>
    )
}

export default AdminLogin
