import React from 'react'
import {useSelector} from 'react-redux'
import Admin from './Admin'
import Member from './Member'
import '../index.css'
import {Link} from 'react-router-dom'


const Account = () => {
    
    const curr = useSelector((state)=>state.LoginReducer.currList);
    
   
    const AD=()=>{
        if(curr[0]!==undefined){
                if(curr[0].admin==='yes'){
                    return <Admin/>
                }
                else{
                    return <Member/>
                }
            }
            else{
                return <div className='errorPage'>
                    <div class="d-grid gap-2">
                                <button className='btn btn-primary' ><Link id="logOut" to="/">LOGIN PAGE</Link></button>
                    </div>
                    <h3>404</h3> 
                    <p>Page not found</p>
                </div>
            }
    }

    return (
        <div>
            <AD/>
        </div>
    )
}

export default Account
